import React from "react";

const TweetInput = (props) => {
  const textareaRef = React.useRef(null);

  const sendTweet = React.useCallback(() => {
    if (textareaRef.current) {
      if (textareaRef.current.value === "") return;
      props.addTweet({
        id: new Date().getTime(),
        icon: "ğŸ±",
        displayName: "ã­ã“",
        accountName: "cat",
        content: textareaRef.current.value
      });
    }
    textareaRef.current.value = null;
  }, [textareaRef.current, props.addTweet]);

  return (
    <div className="tweet-input">
      <div>
        <textarea className="tweet-textarea" ref={textareaRef}></textarea>
      </div>
      <div>
        <button className="send-tweet" onClick={sendTweet}>ãƒ„ã‚¤ãƒ¼ãƒˆ</button>
      </div>
    </div>
  )

}

export default TweetInput;